
.yc-panel.withTopBorder {
  border-top: 1px solid #eeeeee;
}
.yc-panel.withTopBorder:first-child {
  border-top: 0;
}
.yc-panel .yc-panel-title .timer {
  float: right;
  color: #999999;
  font-size: 12px;
}
.yc-panel .job {
  padding-left: 2rem;
  color: #999999;
  font-size: 12px;
}
.yc-panel .text {
  padding-left: 2rem;
  margin-top: 10px;
  font-size: 14px;
  line-height: 1rem;
}



